//   (   )
.more__backdrop {
    @extend %backdrop;
}

//    
.more__backdrop.is-hidden .modal-more {
    //       10%
    @extend %closed_modal;
}

//   
.modal-more {
    @extend %modal;
    height: 480px;
}

//      
.button-modal-more {
    @extend %modal-button-close;
}

//     
.icon-modal-more {
    @extend %modal-icon-close;
}

//  
.more__header {
    width: 180px;
    height: 24px;
    margin: 18px auto 0 auto;

    font-family: $font-header;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    color: $color;
}

//   video
.more-video {
    display: flex;
    width: 224px;
    height: 199px;
    margin-top: 20px;
    margin-left: auto;
    margin-right: auto;
    border-radius: 10px;
    background-color: $color;
}

//   play
.icon-modal-play {
    display: block;
    width: 36px;
    height: 36px;
    margin: auto;
}

//    
.modal-more-text {
    width: 224px;
    margin: 20px auto 0 auto;

    font-family: $font-main;
    font-weight: 400;
    font-size: 14px;
    line-height: 19.6px;
    text-align: left;
}

// ===    480  767 
@media screen and (min-width: $mobile480) {
    .modal-more {
        width: 400px;
    }
}

// ===    768  1200 
@media screen and (min-width: $tablet) {
    .modal-more {
        width: 568px;
    }

    .icon-modal-more {
        width: 24px;
        height: 24px;
    }

    //  
    .more__header {
        width: 300px;
        height: 28px;
        margin: 32px auto 0 auto;
    
        font-size: 20px;
        line-height: 28px;
    }
    
    //   video
    .more-video {
        display: flex;
        width: 448px;
        height: 205px;
        margin: 32px auto 0 auto;
    }
    
    //    
    .modal-more-text {
        width: 448px;
        margin: 32px auto 0 auto;

        font-size: 16px;
        line-height: 22.4px;
    }
}

// ===    1200  
@media screen and (min-width: $desktop) {
    .modal-more {
        width: 800px;
    }
}