//    
%list {
    list-style: none;
    padding: 0px;
    margin: 0px;
    text-decoration: none;
}

// =======================================================
@import './variables';

// ===  
//   (   )
%backdrop {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;

    width: 100%;
    height: 100%;

    background-color: rgba(0, 0, 0, 0.50);

    opacity: 1;
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);

    &.is-hidden {
        opacity: 0; //       .is-hidden
        pointer-events: none; //      
    }
}

//    
%closed_modal {
    //       10%
    transform: translate(-50%, -50%) scale(0.1);
}

//   
%modal {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 280px;
    height: 90%;
    padding: 10px;
    margin-left: auto;
    margin-right: auto;

    border-radius: 10px;
    border: solid 2px $color-accent;

    background-color: $color-white;
    opacity: 1;

    transform: translate(-50%, -50%) scale(1);
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);

    overflow-y: scroll;
}

//      
%modal-button-close {
    display: inline-flex;
    margin: 0;
    padding: 0;
    border: 0;
    background-color: transparent;
}

//     
%modal-icon-close {
    position: absolute;
    top: 12px;
    right: 12px;
    width: 20px;
    height: 20px;
    
    fill: $color;
    cursor: pointer;
}