
.reviews-header {
    margin: 0;
    padding: 0;
    transform: scale(0);
    font-size: 0;
}

.reviews-wrapper {
    overflow: hidden; //    

    width: 320px;
    height: 923px;
    margin-left: auto;
    margin-right: auto;


    background-color: $bkg-color;
    background-size: 885px;
    background-position-x: 100px;
    background-position-y: -1068px;
    background-image: url('../images/gallery/gallery-bcg-mobile.jpg');

}

//       480 
@media (min-device-pixel-ratio: 2),
(min-resolution: 192dpi),
(min-resolution: 2dppx) {
    .reviews-wrapper {
        background-image: url('../images/gallery/gallery-bcg-mobile@2x.jpg');
    }
}

// ===    - 
.slider-container {
    position: relative;
    width: 320px; //   
    height: 465px; //   
    margin-top: 44px;
}

.switch input {
    display: none;
}

.controls {
    position: absolute;
    bottom: 0px;
    left: 124px;
    display: flex;
    gap: 18px;
    cursor: pointer;
    z-index: 1;
}

//    
.controls label {
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background-color: $color-dots;
    transition: .5s;
    cursor: pointer;
}

//    
.switch input:nth-child(1):checked~.controls label:nth-child(1),
.switch input:nth-child(2):checked~.controls label:nth-child(2),
.switch input:nth-child(3):checked~.controls label:nth-child(3) {
    background: $bkg-aux;
}

.slider-wrapper {
    position: relative;
    width: 960px; //    
    height: 442px; //    
    display: flex;
    transition: .5s;
}

.slider-box {
    width: 320px; //  
    height: 442px; //  
    justify-content: center;
}

//   
.switch input:nth-child(1):checked~.slider-wrapper {
    left: 0;
}

.switch input:nth-child(2):checked~.slider-wrapper {
    left: -320px;
}

.switch input:nth-child(3):checked~.slider-wrapper {
    left: -640px;
}
// ===    - 

// ===    - 
.slide-photo {
    width: 85px;
    height: 85px;
    margin-left: auto;
    margin-right: auto;
}

.text-container {
    display: flex;
    width: 280px;
    height: 256px;
    margin-top: 32px;
    margin-left: auto;
    margin-right: auto;
    justify-content: center;
    align-items: center;

    border-radius: 10px;
    background-color: $bkg-aux;
}

.slide-text {
    width: 202px;

    font-family: $font-main;
    font-weight: 400;
    font-size: 12px;
    line-height: 30.6px;
    color: $color-white;
    text-align: center;
}

.slide-footer {
    margin-top: 32px;
    margin-bottom: 0;

    font-family: $font-main;
    font-weight: 500;
    font-size: 20px;
    line-height: 37.2px;
    color: $color;
    text-align: center;
}
