//   (   )
.location__backdrop {
    @extend %backdrop;
}

//    
.location__backdrop.is-hidden .modal-location {
    //       10%
    @extend %closed_modal;
}

//   
.modal-location {
    @extend %modal;
    height: 365px;
    overflow-y: none;
}

//      
.button-modal-location {
    @extend %modal-button-close;
}

//     
.icon-modal-location {
    @extend %modal-icon-close;
}

//  
.location__header {
    width: 180px;
    height: 24px;
    margin: 18px auto 0 auto;

    font-family: $font-header;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    color: $color;
}

//   
.map-wrapper {
    width: 224px;
    height: 199px;
    border-radius: 10px;
    margin: 20px auto 0 auto;
}

//    
.button-map {
    display: block;
        width: 134px;
        height: 42px;
        margin: 24px auto 0 auto;
    
        font-family: $font-main;
        font-weight: 600;
        font-size: 12px;
        line-height: 14,63px;
        text-align: center;
    
        border: none;
        border-radius: 10px;
        background-color: $color-accent;
        cursor: pointer;
}

// ===    480  767 
@media screen and (min-width: $mobile480) {
    .modal-location {
        width: 400px;
    }
}

// ===    768  1200 
@media screen and (min-width: $tablet) {
    .modal-location {
        width: 568px;
        height: 470px;
    }

    //     
    .icon-modal-location {
        width: 24px;
        height: 24px;
    }

    //  
    .location__header {
        width: 322px;
        height: 28px;
        margin: 32px auto 0 auto;

        font-size: 20px;
        line-height: 28px;
        text-align: center;
        color: $color;
    }

    //   
    .map-wrapper {
        width: 448px;
        height: 260px;
        margin: 32px auto 0 auto;
    }
    
    //    
    .button-map {
        width: 145px;
        margin: 32px auto 0 auto;
    
        font-size: 14px;
        line-height: 17.07px;
    }
}

// ===    1200  
@media screen and (min-width: $desktop) {
    .modal-location {
        width: 800px;
    }
}