//   (   )
.franchise__backdrop {
    @extend %backdrop;
}

//    
.franchise__backdrop.is-hidden .modal-franchise {
    //       10%
    @extend %closed_modal;
}

//   
.modal-franchise {
    @extend %modal;
    max-height: 700px;
    padding: 28px 23px 28px 23px;
}

//      
.button-modal-franchise {
    @extend %modal-button-close;
}

//     
.icon-modal-franchise {
    @extend %modal-icon-close;
}

//  
.franchise__header {
    width: 225px;
    height: 72px;
    margin: 0 auto 0 auto;

    font-family: $font-header;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    color: $color;
    letter-spacing: normal;
}

//   
.franchise-text {
    width: 230px;
    height: 240px;
    margin: 20px auto 0 auto;

    font-family: $font-main;
    font-weight: 400;
    font-size: 14px;
    line-height: 19.6px;
}

// ===   
.franchise__form {
    position: relative;
    width: 224px;
    height: 221px;
    padding: 0;
    margin: 32px 0 32px 0;
    margin-left: auto;
    margin-right: auto;
}

// -  
.franchise__name {
    position: absolute;
    top: 4px;
    left: 12px;
}

.franchise__phone {
    position: absolute;
    top: 48px;
    left: 12px;
}

.franchise__email {
    position: absolute;
    top: 92px;
    left: 12px;
}

.franchise__comment {
    position: absolute;
    top: 135px;
    left: 12px;
}

// -  input
.franchise__input {
    width: 224px;
    height: 32px;
    padding-left: 65px;

    border-radius: 10px;
    border: solid 1px $bkg-aux;

    font-family: $font-main;
    font-weight: 400;
    font-size: 12px;
    line-height: 16.8px;
    background-color: $color-accent-rgba;
}

.franchise__input.phone {
    margin-top: 12px;
}

.franchise__input.textarea {
    height: 65px;
    margin-top: 12px;
    padding: 5px;
    text-indent: 90px;
}

// -    
.franchise__submit {
    display: block;
    width: 114px;
    height: 42px;
    margin: 24px auto 0 auto;

    font-family: $font-main;
    font-weight: 600;
    font-size: 12px;
    line-height: 14.63px;
    text-align: center;

    border: none;
    border-radius: 10px;
    background-color: $color-accent;
    cursor: pointer;
}

// ===    480  767 
@media screen and (min-width: $mobile480) {
    .modal-franchise {
        width: 400px;
    }
}

// ===    768  1200 
@media screen and (min-width: $tablet) {
    .modal-franchise {
        width: 568px;
        max-height: 670px;
        padding: 32px;
    }

    //     
    .icon-modal-franchise {
        width: 24px;
        height: 24px;
    }
    
    //  
    .franchise__header {
        width: 330px;
        height: 84px;
        margin: 0 auto 0 auto;
    
        font-size: 20px;
        line-height: 28px;
    }
    
    //   
    .franchise-text {
        width: 480px;
        height: 132px;
        margin: 24px auto 0 auto;
    
        font-size: 16px;
        line-height: 22.4px;
    }

    // ===   
    .franchise__form {
        position: relative;
        width: 368px;
        height: 312px;
        padding: 0;
        margin: 32px 0 0px 0;
        margin-left: auto;
        margin-right: auto;
     }
    
    // -  
    .franchise__name {
        position: absolute;
        top: 8px;
        left: 16px;
    }
    
    .franchise__phone {
        position: absolute;
        top: 67px;
        left: 16px;
    }
    
    .franchise__email {
        position: absolute;
        top: 125px;
        left: 16px;
    }
    
    .franchise__comment {
        position: absolute;
        top: 175px;
        left: 16px;
    }
    
    // -  input
    .franchise__input {
        width: 368px;
        height: 42px;
        padding-left: 70px;
    
        font-size: 14px;
        line-height: 19,6px;

    }
    
    .franchise__input.phone {
        margin-top: 16px;
    }
    
    .franchise__input.textarea {
        height: 65px;
        margin-top: 16px;
        padding: 5px;
        text-indent: 95px;
    }
    
    // -    
    .franchise__submit {
        display: block;
        width: 114px;
        height: 42px;
        margin: 24px auto 0 auto;
    
        font-family: $font-main;
        font-weight: 600;
        font-size: 12px;
        line-height: 14.63px;
    }
}

// ===    1200  
@media screen and (min-width: $desktop) {
    .modal-franchise {
        width: 800px;
    }
}